<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-03 19:43:40
 * @LastEditTime: 2019-10-16 18:02:58
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <!-- 标题 -->
    <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('Employer_detail')}}</p>
    </div>
    <!-- 主要内容 -->
    <div class="contain">
      <p>{{$t('dh_helper_pa_desc21')}}</p>
      <div class="table_address">
        <div class="content">
          <div
            class="terntory"
            :class="{blank:address.Territory!='Territory'}"
            @click="showPicker('Territory')"
          >{{address.Territory}}</div>
          <div
            class="distnct"
            :class="{blank:address.District!='District'}"
            @click="showPicker('District')"
          >{{address.District}}</div>
          <div class="block">
            <input type="text" v-model="address.block" placeholder="Block" />
            <input type="text" v-model="address.floor" placeholder="Floor" />
            <input type="text" v-model="address.room" placeholder="Room" />
          </div>
          <div class="building">
            <input type="text" v-model="address.building" placeholder="Building/Estate/Street No*" />
          </div>
        </div>
      </div>

      <div class="form-item">
        <p>{{$t('hl_employer_desc23')}}</p>
        <!-- @blur="genderBlur" -->
        <div
          @click="showPicker('Industry')"
          :class="{blank:address.Industry!='Industry'}"
          class="GenderDiv"
          ref="Industry"
        >{{this.address.Industry}}</div>
      </div>
      <p>*Required</p>
    </div>
    <!-- 底部按钮 -->
    <div class="button-bar">
      <button @click="confirm" class="proceed">{{$t('info_doc_data_next')}}</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      address: {
        Territory: "Territory",
        District: "District",
        block: "",
        floor: "",
        room: "",
        building: "",
        Industry: "Industry"
      }
    };
  },
  created(){
    this.getProtectorInfo()
  },
  computed: {
    ...mapState(["userAddress"])
  },
  methods: {
    ...mapMutations(["setUserAddress"]),
    //点击选择性别---------------------------------------------------/////
    showPicker(type) {
      this.data_type_two = type;
      let column1 = [];
      if (type == "Territory") {
        column1 = [
          { text: "NEW TERRITORIES", value: "NEW TERRITORIES" },
          { text: "HONG KONG", value: "HONG KONG" },
          { text: "KOWLOON", value: "KOWLOON" }
        ];
      } else if (type == "District") {
        column1 = [
          { text: "North District", value: "North District" },
          { text: "Tai Po", value: "Tai Po" },
          { text: "Tsuen Wan", value: "Tsuen Wan" },
          { text: "Kai Hing", value: "Kai Hing" }
        ];
      } else if (type == "Industry") {
        column1 = [
          { text: "Accounting & Auditing", value: "Accounting" },
          {
            text: "Advertising,Media & Public Relations",
            value: "Advertising"
          },
          { text: "Amusement & Entertainment", value: "Amusement" },
          { text: "Archiecture & Surveying", value: "Archiecture" },
          { text: "Banking & Finance", value: "Banking" }
        ];
      }
      //改变插件中的默认title和data
      setTimeout(() => {
        this.picker.$updateProps({
          title: type,
          data: [column1]
        });
      }, 0);

      //改变
      setTimeout(() => {
        this.picker.$updateProps({
          title: type,
          data: [column1]
        });
      }, 0);
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: this.$t("Gender"),
          data: [column1],
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectGender,
          onCancel: this.cancelGender
        });
      }
      this.picker.show();
    },
    //选择性别点击确认事件
    selectGender(selectedVal, selectedIndex, selectedText) {
      this.address[this.data_type_two] = selectedText.join(" ");
    },
    //选择性别点击取消事件
    cancelGender() {},

    //点击确认按钮------------------------------------------------------///////
    confirm() {
      console.log(this.address);
      //验证信息是否都填写完毕
      if (
        this.address.Territory == "Territory" ||
        this.address.District == "District" ||
        this.address.block == "" ||
        this.address.floor == "" ||
        this.address.room == "" ||
        this.address.building == "" ||
        this.address.Industry == "Industry" 
      ) {
        this.$createToast({
          txt: this.$t("notInformation"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //将地址信息保存到vuex中
      this.setUserAddress(this.address);
      this.$router.push("/HelperDetails");
    },
    toPrePage() {
      this.$router.push("/Employer");
    },
    //如果vuex中有保单人信息,获取渲染到页面-----------------------------///
    getProtectorInfo() {
      console.log(this.userAddress);
      //console.log(JSON.parse(JSON.stringify(this.helperInfo))["address"]);
      if (this.userAddress) {
        for (var key in this.userAddress) {
          this.address[key] = this.userAddress[key];
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: rgba(240, 240, 240);
  padding-bottom: 260px;
  position: relative;
  padding-top: 120px;
  height: 100%;
  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    width: 100%;
    height: 200px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(240, 240, 240);
  }
  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    margin-top: 50px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(240, 240, 240);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
  .contain {
    p {
      font-size: 30px;
      margin-top: 50px;
    }
    .table_address {
      width: 670px;
      margin-top: 20px;
      background-color: #fff;
      border: 2px solid rgba(23, 37, 54, 0.3);
      .content {
        margin-left: 20px;
        width: 649px;
        input {
          height: 80px;
          width: 100%;
          color: #000;
          font-weight: bold;
        }
        input::-webkit-input-placeholder {
          color: #9b9b9b;
          letter-spacing: 2px;
          font-weight: 500;
        }
        .terntory {
          width: 100%;
          height: 81px;
          color: #9b9b9b;
          line-height: 81px;
          border-bottom: 1px solid rgba(23, 37, 54, 0.8);
          font-size: 20px;
        }
        .distnct {
          width: 100%;
          height: 81px;
          color: #9b9b9b;
          line-height: 81px;
          border-bottom: 1px solid rgba(23, 37, 54, 0.8);
          font-size: 20px;
        }
        .blank {
          color: rgb(0, 0, 0);
          font-weight: bold;
        }

        .block {
          width: 100%;
          display: flex;
          height: 61px;
          padding: 10px 0;
          border-bottom: 1px solid rgba(23, 37, 54, 0.8);
          input {
            flex: 1;
            height: 60px;
            border-right: 1px solid rgba(23, 37, 54, 0.8);
            &:last-child {
              border-right: 0;
              padding-left: 10px;
            }
            &:nth-child(2) {
              padding-left: 10px;
            }
          }
        }
        .building {
          width: 100%;
        }
      }
    }
    .industry {
      height: 80px;
      width: 670px;
      border-radius: 10px;
      background-color: #fff;
      margin-top: 20px;
      input {
        width: 650px;
        display: block;
        height: 100%;
        border: 2px solid rgba(23, 37, 54, 0.5);
        border-radius: 10px;
        padding-left: 20px;
      }
    }
    .form-item {
      p {
        color: rgb(0, 0, 0);
        margin-bottom: 20px;
      }
      margin-top: 20px;
    }
    .GenderDiv {
      height: 60px;
      width: 660px;
      border-radius: 10px;
      font-size: 20px;
      background-color: #fff;
      border: 2px solid rgba(155, 155, 155, 0.5);
      color: #9b9b9b;
      line-height: 60px;
      text-align: left;
      padding-left: 10px;
    }
    .blank {
      color: rgb(0, 0, 0);
      font-weight: bold;
    }
  }
}
</style>


